*,
*::after,
*::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-size: 16px;
  line-height: 22px;
  font-family: "Open-sans", "Arial", sans-serif;
  color: #48494d; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 1180px;
  margin: 0 auto; }

.button {
  display: block;
  padding: 17px 19px;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  border: none;
  background-color: #d12136;
  color: #ffffff; }

.page-header {
  background-color: #f5f5f9; }

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 84px;
  padding-left: 6px; }

.page-header__logo-link {
  padding-top: 5px; }

.site-list__link {
  font-weight: 600;
  text-decoration: none;
  color: #48494d; }

.page-header__logo-img {
  width: 133px;
  height: 55px; }

.site-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding-right: 5px; }

.site-list__item {
  margin-left: 69px; }
  .site-list__item:first-child {
    margin-left: 0; }

.page-main__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 74px; }

.gallery {
  width: 600px; }

.gallery__slider {
  margin-top: 16px; }

.gallery__preview {
  margin-top: 39px; }

.slider {
  position: relative; }

.slider__list {
  padding: 0;
  margin: 0;
  list-style-type: none; }

.slider__item {
  display: none; }
  .slider__item--active {
    display: block; }

.slider__img {
  display: block; }

.slider__new-model {
  position: absolute;
  display: block;
  padding: 7px 10px 10px 10px;
  text-align: center;
  width: 122px;
  background-color: #d12136;
  color: #ffffff;
  top: 0;
  font-weight: 600;
  font-size: 10px;
  line-height: 14px;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.preview {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }

.preview__btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  border: 1px solid #d7d9df;
  background-color: transparent;
  cursor: pointer; }
  .preview__btn::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 13px;
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-size: contain; }
  .preview__btn--prew::before {
    transform: rotate(180deg); }
  .preview__btn:disabled {
    cursor: default; }
  .preview__btn:disabled::before {
    opacity: 0.4; }

.preview__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  width: 435px;
  padding: 0;
  list-style-type: none; }

.preview__img {
  display: block;
  width: 128px;
  height: 80px; }

.info {
  width: 525px; }

.info__title {
  padding: 0;
  margin: 0;
  font-size: 30px;
  line-height: 41px;
  font-weight: 700; }

.info__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 310px;
  margin: 0;
  margin-top: 21px;
  padding: 0;
  list-style-type: none; }

.info__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 70px; }
  .info__item--transmission {
    width: 76px; }

.info__descr {
  padding: 0;
  margin: 0; }

.info__price-block {
  display: flex;
  align-items: flex-end;
  margin-top: 36px; }

.info__price-new {
  padding: 0;
  margin: 0;
  font-size: 35px;
  line-height: 48px;
  font-weight: 700;
  margin-right: 33px; }

.info__price-old {
  position: relative;
  margin: 0;
  margin-bottom: 7px;
  padding: 0;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 20px;
  line-height: 27px;
  color: #a3a4a6; }
  .info__price-old::before {
    content: "";
    position: absolute;
    top: 12px;
    width: 100%;
    height: 1px;
    transform: rotate(-6deg);
    background-color: #a3a4a6; }

.info__btn {
  margin-top: 39px; }
  .info__btn--white {
    margin-top: 16px;
    padding: 15px 19px;
    border: 1px solid #d12136;
    background-color: #ffffff;
    color: #d12136;
    letter-spacing: 0.1em; }
